import React, { useEffect } from "react";
import { Route, Switch } from "react-router-dom";
import { useDispatch } from "react-redux";
import { setWindowWidth } from "../store/actions/generalAction";
import LandingPage from "../pages/LandingPage";
import Logo from "../components/UiComponents/Logo";
import ContactUs from "../pages/ContactUs";
import Project from "../pages/Project";
import ProjectAdmin from "../pages/admin/Project";
import SignIn from "../pages/admin/SignIn";

const App = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(setWindowWidth(window.innerWidth));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <div className="app">
      <Logo />
      <Switch>
        <Route path="/contact" component={ContactUs} />
        <Route path="/project/:id" component={Project} />
        <Route exact={true} path="/" component={LandingPage} />
        <Route exact={true} path="/admin" component={SignIn} />
        <Route path="/admin/project/:id" component={ProjectAdmin} />
      </Switch>
    </div>
  );
};

export default App;
