import React from "react";
import { firstNameObj, passwordObj } from "../../../helpers/FormObjects";
import Form from "../../../components/UiComponents/Form";

const initValues = {
  firstName: "",
  password: "",
};
const fields = [firstNameObj, passwordObj];

const ContactUs = (props) => {
  const handleClick = () => {
    return props.history.goBack();
  };
  return (
    <div className="contact_page">
      <div className="contact_page_content">
        <button type="button" className="exit_form" onClick={handleClick}>
          Exit
        </button>
        <h3>Sign-in for admin access</h3>
        <Form fields={fields} initValues={initValues} submitValue="send" />
      </div>
    </div>
  );
};

export default ContactUs;
