import React from "react";
import "./index.scss";

const TextBlock = ({ text, title, cols }) => {
  const textClass = cols === 6 ? "text_6" : "text_12";
  return (
    <div className={`text_block ${textClass}`}>
      {title && <h4>{title}</h4>}
      <p>{text}</p>
    </div>
  );
};

export default TextBlock;
